import React from "react";
import { CardColumns, Card, Container, CardDeck } from "react-bootstrap";

const Categorias = () => {
  return (
    <Container>
      <CardDeck>
        <Card className="bg-dark text-white" border="light">
          <Card.Img src={require("../images/6.png")} alt="Card imagen" />
          <Card.ImgOverlay>
            <button type="button" class="btn btnCategoria btn-outline-dark">
              Jeans
            </button>
          </Card.ImgOverlay>
        </Card>
        <Card className="bg-dark text-white" border="light">
          <Card.Img src={require("../images/7.png")} alt="Card image" />
          <Card.ImgOverlay>
             <button type="button" class="btn btnCategoria btn-outline-dark">
              Conjunto
            </button>
          </Card.ImgOverlay>
        </Card>
        <Card className="bg-dark text-white" border="light">
          <Card.Img src={require("../images/8.png")} alt="Card image" />
          <Card.ImgOverlay>
             <button type="button" class="btn btnCategoria btn-outline-dark">
              Blusas
            </button>
          </Card.ImgOverlay>
        </Card>
      </CardDeck>
    </Container>
  );
};

export default Categorias;
